import { all, takeEvery, put, call, fork } from 'redux-saga/effects';
import { push } from 'connected-react-router';
import actions from './actions';
import { clearToken, getToken } from '../../helpers/utility';
import AuthHelper from '../../helpers/authHelper';
import notification from '../../components/notification';

const fakeApiCall = 'false'; // auth0 or express JWT

export function* loginRequest() {
  yield takeEvery('LOGIN_REQUEST', function*({ payload }) {
  console.log('loginRequest:', payload);

  if (fakeApiCall === 'true') {
    yield put({
      type: actions.LOGIN_SUCCESS,
      token: 'secret token',
      profile: 'Profile',
    });
  }

  if (fakeApiCall === 'false') {

    const userInfo = {
      username: document.getElementById('inputUserName').value || '',
      password: document.getElementById('inpuPassword').value || '',
    };

    try {
      const result = yield call(AuthHelper.login, userInfo);
      console.log('result:', result);

      if(result.error){
        throw new Error ("Error: " + result.error);
      }

      if(result.token){
		    const token = result.token;

				console.log('result.token:', token); 

		    yield put({
		      type: actions.LOGIN_SUCCESS,
		      token: token,
		      profile: 'Profile',
		    });
      }

    } catch(err) {
      console.log('err:', err);
      notification('error', err.message);
      yield put({
        type: actions.LOGIN_ERROR
      });
    }

  }
  });
}

export function* loginSuccess() {
  yield takeEvery(actions.LOGIN_SUCCESS, function*(payload) {
    console.log('loginSuccess payload:', payload);
    yield localStorage.setItem('id_token', payload.token);
  });
}

export function* loginError() {
  //console.log('loginError:', '');
  yield takeEvery(actions.LOGIN_ERROR, function*() {});
}

export function* logout() {
  yield takeEvery(actions.LOGOUT, function*() {
    console.log('logout:', '');
    clearToken();
    yield put(push('/signin'));
  });
}
export function* checkAuthorization() {
  yield takeEvery(actions.CHECK_AUTHORIZATION, function*() {
    const token = getToken().get('idToken');
    console.log('checkAuthorization token: ', token);
    const token_expire = AuthHelper.checkExpirity(token);
    console.log('checkExpirity token_expire', token_expire);
    if (token) {
      yield put({
        type: actions.LOGIN_SUCCESS,
        token,
        profile: 'Profile',
      });
    }
  });
}
export default function* rootSaga() {
  yield all([
    fork(checkAuthorization),
    fork(loginRequest),
    fork(loginSuccess),
    fork(loginError),
    fork(logout),
  ]);
}
