import React, { Suspense } from 'react';
import { Route, Redirect } from 'react-router-dom';
import { ConnectedRouter } from 'connected-react-router';
import { connect } from 'react-redux';
//import App from './containers/App/App';
//import asyncComponent from './helpers/AsyncFunc';
import Auth0 from './helpers/auth0';

//console.log('DEBUG_File', new Error().stack);

const RestrictedRoute = ({ component: Component, isLoggedIn, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      isLoggedIn ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: '/signin',
            state: { from: props.location },
          }}
        />
      )
    }
  />
);

function WaitingComponent(Component) {
  return props => (
    <Suspense fallback={<div><div className="loader"><p></p></div></div>}>
      <Component {...props} />
    </Suspense>
  );
}

//const App = asyncComponent(() => import("./containers/App/App"));
//const Base = asyncComponent(() => import("customApp/containers/LandingPage"));
const App = React.lazy(() => import("./containers/App/App"));
const LandingPage = React.lazy(() => import("customApp/containers/LandingPage"));
const Aviator = React.lazy(() => import("customApp/containers/Aviator"));
const Weather = React.lazy(() => import("customApp/containers/Weather"));
const a404 = React.lazy(() => import("./containers/Page/404"));
const a500 = React.lazy(() => import("./containers/Page/500"));
const signin = React.lazy(() => import("./containers/Page/signin"));
const signup = React.lazy(() => import("./containers/Page/signup"));
const forgotPassword = React.lazy(() => import("./containers/Page/forgotPassword"));
const resetPassword = React.lazy(() => import("./containers/Page/resetPassword"));

const routes = [
  {
    path: "/",
    component: WaitingComponent(LandingPage)
  },
  {
    path: "/aviator",
    component: WaitingComponent(Aviator)
  },
  {
    path: "/weather",
    component: WaitingComponent(Weather)
  },
  {
    path: "/404",
    component: WaitingComponent(a404)
  },
  {
    path: "/500",
    component: WaitingComponent(a500)
  },
  {
    path: "/signin",
    component: WaitingComponent(signin)
  },
  {
    path: "/signup",
    component: WaitingComponent(signup)
  },
  {
    path: "/forgotpassword",
    component: WaitingComponent(forgotPassword)
  },
  {
    path: "/resetpassword",
    component: WaitingComponent(resetPassword)
  }
];

const routeComponents = routes.map(({path, component}, key) => <Route exact path={path} component={component} key={key} />);

const PublicRoutes = ({ history, isLoggedIn }) => {
  return (
    <ConnectedRouter history={history}>
      <div id="router">

        {routeComponents}

        <Route
          path="/auth0loginCallback"
          render={props => {
            Auth0.handleAuthentication(props);
          }}
        />
        <RestrictedRoute
          path="/dashboard"
          component={WaitingComponent(App)}
          isLoggedIn={isLoggedIn}
        />
      </div>
    </ConnectedRouter>
  );
};

export default connect(state => ({
  isLoggedIn: state.Auth.idToken !== null,
}))(PublicRoutes);
